import React, { Component } from 'react';
// import axios from 'axios';
import Header from '../../components/Header/Header';
import Form from '../../components/Form/Form';
import AsyncComponent from '../../components/AsyncCompnent/AsyncComponent';
import Preloader from '../../components/Preloader/Preloader';

import classes from './App.module.css';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      input: true,
      data: {
        region: {
          name: null,
          avgAge: null,
          avgDailyIncomeInUSD: null,
          avgDailyIncomePopulation: null
        },
        periodType: null,
        timeToElapse: null,
        reportedCases: null,
        population: null,
        totalHospitalBeds: null
      },
      results: null
    };
  }

  // eslint-disable-next-line class-methods-use-this
  getInputValues(event) {
    const name = event.target.getAttribute('name');
    const value = Number.parseFloat(event.target.value) || event.target.value;
    return { name, value };
  }

  formatNumber = (x) => (x ? x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',') : x);

  regionalInputHandler = (event) => {
    const { name, value } = this.getInputValues(event);
    this.setState((prevState) => {
      let { data } = prevState;
      let { region } = data;
      region = { ...region, ...{ [name]: value } };
      data = { ...data, region };
      return { data };
    });
  };

  otherInputHandler = (event) => {
    const { name, value } = this.getInputValues(event);
    this.setState((prevState) => {
      let { data } = prevState;
      data = { ...data, ...{ [name]: value } };
      return { data };
    });
  };

  estimate = async () => {
    this.setState({ input: false });
    const { data } = this.state;
    const url = 'https://www.andela-challenge5.herokuapp.com/api/v1/on-covid-19';
    // const url = 'http://localhost:4500/api/v1/on-covid-19';
    const response = await fetch(url, {
      method: 'POST', // *GET, POST, PUT, DELETE, etc.
      mode: 'cors', // no-cors, *cors, same-origin
      cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
      credentials: 'same-origin', // include, *same-origin, omit
      headers: {
        'Content-Type': 'application/json'
        // 'Content-Type': 'application/x-www-form-urlencoded',
      },
      redirect: 'follow', // manual, *follow, error
      referrerPolicy: 'no-referrer', // no-referrer, *no-referrer-when-downgrade, origin, origin-when-cross-origin, same-origin, strict-origin, strict-origin-when-cross-origin, unsafe-url
      body: JSON.stringify(data) // body data type must match "Content-Type" header
    });
    const results = await response.json();
    this.setState({ results });
  }

  render() {
    const { input } = this.state;
    const { results } = this.state;
    let output;
    if (input) {
      output = (
        <Form
          regionDataChanged={this.regionalInputHandler}
          otherInputChanged={this.otherInputHandler}
          clicked={this.estimate}
        />
      );
    } else if (!input && results) {
      const Display = AsyncComponent(() => import('../../components/Display/Display'));
      output = (
        <Display format={this.formatNumber} results={results} />
      );
    } else {
      output = <Preloader />;
    }
    return (
      <div className={classes.App}>
        <Header heading={classes.Heading} />
        {output}
      </div>
    );
  }
}

export default App;
